  computed: {
    countUsed () {
      let hadMoney = this.zuanList[0].num*2 + this.zuanList[1].num*20 + this.zuanList[2].num*200 + this.zuanList[3].num*3300
      let usedMoney = this.zuanMoney.moneyInit - this.zuanMoney.allMoney
      // console.log(hadMoney, usedMoney, this.zuanMoney.allMoney, usedMoney)
      return (usedMoney - hadMoney)
    },
    countYuan () {
      let hadMoney = this.zuanList[0].num*2 + this.zuanList[1].num*20 + this.zuanList[2].num*200 + this.zuanList[3].num*3300
      let usedMoney = this.zuanMoney.moneyInit - this.zuanMoney.allMoney
      return ((usedMoney - hadMoney)/10).toFixed(2)
    },
    countLianUsed () {
      let hadMoney = this.lianList[0].num*20 + this.lianList[1].num*100
      let usedMoney = this.lianHadSheng
      return (hadMoney - usedMoney)
    },
    countLianYuan () {
      let hadMoney = this.lianList[0].num*20 + this.lianList[1].num*100
      let usedMoney = this.lianHadSheng
      return ((hadMoney - usedMoney)/10).toFixed(2)
    },
    countXiaohao (){
      if(!this.lianLock.lockOne && !this.lianLock.lockTwo && !this.lianLock.lockThree && !this.lianLock.lockFour){
        this.lianXiaohao = 193
      }else{
        this.lianXiaohao = 0
        if(this.lianLock.lockOne == true){
          this.lianXiaohao += this.xiaoHaoList.one
        }
        if(this.lianLock.lockTwo == true){
          this.lianXiaohao += this.xiaoHaoList.two
        }
        if(this.lianLock.lockThree == true){
          this.lianXiaohao += this.xiaoHaoList.three
        }
        if(this.lianLock.lockFour == true){
          this.lianXiaohao += this.xiaoHaoList.four
        }
        if(this.lianLock.lockThreeBig == true){
          this.lianXiaohao *= 2
        }
      }
    }
  },
  methods: {
    getList (page) {
      //
      let params = this.request

      if(parseInt(page) > 0){
        params.page = page;
      }
      console.log(params)
      var vue = this;
      const url = this.$api_host+'getList.py'
      this.$axios.get(url, {
        params: params,
        paramsSerializer: function(params) {
          return vue.$qs.stringify(params, {arrayFormat: 'brackets'})
        },
      }).then(function(response){
        console.log(response)
        if(response.status == 200){
          if(vue.request.page > 1){
            for(let item in response.data.list){
              vue.bangList.push(response.data.list[item])
            }
          }else{
            vue.bangList = response.data.list
          }
          vue.totalNumber = response.data.totalNumber;
        }else{
          vue.$Message.warning('');
        }
      }).catch(function(error){
        vue.$Message.error(error);
      });
    },
    handleReachBottom () {
      const _this = this
      const max_page = Math.round(_this.totalNumber/_this.request.page)
      let page = ++this.request.page
      if (max_page < page) {
        this.$Message.warning('^--^')
      }

      setTimeout(function () {
        _this.getList(page)
      }, 500)
    },
    selectData () {
      this.select_modal = true
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.getList(1)
          this.$Message.success('!');
          this.select_modal = false
        } else {
          this.$Message.error('fail!');
        }
      })
    },
    handleReset (name) {
      // this.$refs[name].resetFields()
      this.select_modal = false
    },
    setOrder (name) {
      switch (parseInt(name)) {
        case 5:
          //
          this.request.orderby = 'all_abilities desc'
          this.orderName = ''
          break;
        case 4:
          //
          this.request.orderby = 'equipment desc'
          this.orderName = ''
          break;
        case 3:
          this.request.orderby = 'abilities desc'
          this.orderName = ''
          //
          break;
        case 2:
          this.request.orderby = 'id desc'
          this.orderName = ''
          //
          break;
        default:
          this.request.orderby = 'all_abilities desc'
          this.orderName = ''
          //
          break;
      }
      //
      this.bangList = []
      this.getList()
    },
    toSource (name) {
      this.mp3Suc = require('../assets/voice/zuan_suc.mp3')
      switch (parseInt(name)) {
        case 6:
          this.$Message.info('')
          break;
        case 5:
          //
          this.initFiveList()
          this.modalLianhua = true
          break;
        case 4:
          this.modalJiahu = true
          break;
        case 3:
          this.modalCharge = true
          break;
        case 2:
          this.modalBox = true
          break;
        default:
          this.modalValue = true
          //
          break;
      }
    },
    toValue () {
      //
      if(!this.bang_link){
        return this.$Message.warning('')
      }
      //bang_id
      const arrList = this.bang_link.split('/')
      const bang_id = arrList[5]
      var regu =/\d{1,2}_\d{1,10}$/;
      var re = new RegExp(regu);
      var result = re.test(bang_id)
      if(result !== true){
        return this.$Message.warning('')
      }
      //
      var vue = this;
      const url = this.$api_host+'evalueate.py'
      const params = {
        bang_id: bang_id
      }
      //
      this.valueData = []
      this.loading = true
      console.log(this.loading)
      this.$axios.get(url, {
        params: params,
        paramsSerializer: function(params) {
          return vue.$qs.stringify(params, {arrayFormat: 'brackets'})
        },
      }).then(function(response){
        vue.loading = false
        if(response.status == 200){
          vue.valueData = response.data
          console.log(vue.valueData)
        }else{
          vue.$Message.warning('');
        }
      }).catch(function(error){
        vue.$Message.error('');
      });
    },
    liveInit () {
      const vue = this
      const url = this.$api_host+'getLiveList.py'
      this.$axios.get(url).then(function(response){
        if(response.status == 200){
          vue.callBackLive(response.data)
          // console.log(vue.valueData, 222)
        }else{
          vue.$Message.warning('');
        }
      });
    },
    callBackLive (data) {
      const legendData = []
      const seriesData = []
      //echarts
      for(let i in data.liveList){

        legendData.push(data.liveList[i][2])
        seriesData.push({name: data.liveList[i][2], value: data.liveList[i][15]})
      }
      this.liveOption.legend.data = legendData
      this.liveOption.series[0].data = seriesData
    },
    liveShow () {
      this.modalLive = true
      const vue = this
      console.log(this.liveOption)
      const chart1 = this.$echarts.init(document.getElementById("chart1"))
      chart1.setOption(vue.liveOption);
    },
    async openGift () {
      //,
      const length = this.giftList.length;
      //
      const weightArr = []
      let bei = this.optionValue
      console.log(bei, 555)
      for (let i  in this.giftList){
        let number = Math.pow((length - this.giftList[i].value), bei)
        while(number>0){
          weightArr.push(this.giftList[i].value)
          number--
        }
      }
      console.log(weightArr.length)
      //
      weightArr.sort(function (a, b ) {
        return Math.random() > 0.5 ? -1 : 1;
      })

      let giftsGet = []
      while (this.openNumber > 0){
        const key = this.randomKey(weightArr);
        giftsGet.push(this.giftList[key]);
        this.openNumber--;
        this.dealOpenedGifts(giftsGet)
        await this.sleep(1000)
      }
      this.giftDisabled = false
    },
    randomKey(weightArr){
      const rand = Math.floor( Math.random() * (weightArr.length - 1) )
      const middle = weightArr.slice(rand, rand +1)
      return middle[0]
    },
    sleep(millisecond) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve()
        }, millisecond)
      })
    },
    dealOpenedGifts (giftsGet){
      this.giftDisabled = true
      //
      this.giftsGet = []
      //
      let flag = 1
      for(let j in giftsGet){
        flag = 1
        if(giftsGet[j] == undefined)
          continue
        for(let p in this.giftsGet){
          if(this.giftsGet[p].value == giftsGet[j].value){
            this.giftsGet[p].count += 1
            flag = 0
          }
        }
        if(flag == 1){
          //
          giftsGet[j].count = 1
          this.giftsGet.push(giftsGet[j])
        }
      }

      const middle = this.giftsGet
      let newArr = []
      for(let q in middle){
        let xiabiao = parseInt(q/8)
        if(newArr[xiabiao] == undefined){
          newArr[xiabiao] = []
        }
        newArr[xiabiao].push(middle[q])
      }

      this.giftsGet = JSON.parse(JSON.stringify(newArr))
    },
    clearOpen () {
      if(this.giftDisabled == true){
        this.$Message.warning('');
      }
      this.giftsGet = []
      this.openNumber = 200
    },
    addZuan (type) {
      if(this.zuanMoney.allMoney < this.zuanList[type].price){
        this.$Message.warning('');
        return false;
      }
      this.zuanList[type].num++
      this.zuanMoney.allMoney -= this.zuanList[type].price
    },
    addZuanZu (type) {
      if(this.zuanMoney.allMoney < this.zuanList[type].price*20){
        this.$Message.warning('');
        return false;
      }
      this.zuanList[type].num += 20
      this.zuanMoney.allMoney -= this.zuanList[type].price*20
    },
    addJiahu (type) {

      let forward = parseInt(this.jiahuValue/8) + 1;
      if(this.zuanList >= 160){
        this.$Message.warning('')
        return false
      }
      if(this.zuanList[type].section.indexOf(forward) < 0){
        this.$Message.warning('')
        return false
      }

      if(this.zuanList[type].num <= 0){
        this.$Message.warning('')
        return false
      }

      //
      let bei = this.zuanValue
      console.log(bei)
      let length = Math.max(...this.zuanList[type].section) + Math.abs(bei - 4)
      let weightArr = []
      for (let i  in this.zuanList[type].section){
        let number = Math.pow((length - this.zuanList[type].section[i]), bei)
        while(number>0){
          weightArr.push(this.zuanList[type].section[i])
          number--
        }
      }
      // console.log(weightArr)
      //
      weightArr.sort(function (a, b ) {
        return Math.random() > 0.5 ? -1 : 1;
      })

      let key = this.randomKey(weightArr)
      let flag = 1
      switch (type) {
        case 3:
          flag = 18
          break;
        case 2:
          flag = 13
          break;
        case 1:
          flag = 8
          break;
        default:
          flag = 3
          break;
      }
      if(weightArr[key] >= parseInt(this.jiahuValue/8) || parseInt(this.jiahuValue/8) < flag){
        this.jiahuValue += 8
        this.$refs.video.play()
        if(this.jiahuValue == 160){
          this.$Message.success("20!")
        }
      }else{
        this.jiahuValue > 8?this.jiahuValue -= 8:this.jiahuValue = 8
      }
      //
      this.zuanList[type].num--
    },
    addLian (type){
      if(this.lianMoney.allMoney < this.lianList[type].price){
        this.$Message.warning('');
        return false;
      }
      this.lianList[type].num++
      this.lianMoney.allMoney -= this.lianList[type].price
      this.lianHadSheng += this.lianList[type].price
    },
    addLianZu (type) {
      if(this.lianMoney.allMoney < this.lianList[type].price*20){
        this.$Message.warning('');
        return false;
      }
      this.lianList[type].num += 20
      this.lianMoney.allMoney -= this.lianList[type].price*20
      this.lianHadSheng += this.lianList[type].price*20
    },
    //
    lianHua (){
      let xiaohao = this.lianXiaohao
      let had = this.lianHadSheng
      // console.log(xiaohao, had)
      if(xiaohao > had){
        this.$Message.warning('');
        return false
      }
      this.$refs.video.play()
      if(!this.checkNewIsEmpty()){
        this.$Message.warning('')
        return false
      }
      this.lianHadSheng -= this.lianXiaohao
      //
      let min = this.checkLock()
      let start = min > 0 ?min:1
      let tiaoshu = this.rangeInt(min, 5)
      let pool = []
      if(min >= 3){
        pool = [1, 2, 3, 4, 5]
      }else{
        pool = [1, 2, 3, 4]
      }
      //
      if(this.lianLock.lockOne == true){
        this.lianNew.one = this.dealOne()
        pool = this.deleteArr(1, pool)
      }
      if(this.lianLock.lockTwo == true){
        this.lianNew.two = this.dealTwo()
        pool = this.deleteArr(2, pool)
      }
      if(this.lianLock.lockThree == true){
        this.lianNew.three = this.dealThree()
        pool = this.deleteArr(3, pool)
      }
      if(this.lianLock.lockFour == true){
        this.lianNew.four = this.dealFour()
        pool = this.deleteArr(4, pool)
      }
      pool = JSON.parse(JSON.stringify(pool))
      // console.log(pool, 1)
      for(let i = 1; i<=(tiaoshu-min); i++ ){
        //
        let arrSelect = this.checkLianList(pool)
        let q = this.randomKey(arrSelect)
        // let q = arrSelect[key]
        // console.log(arrSelect, q, 2)
        switch (parseInt(q)) {
          case 5:
            this.lianNew.five = this.dealFive()
            break;
          case 4:
            this.lianNew.four = this.dealFour()
            break;
          case 3:
            this.lianNew.three = this.dealThree()
            break;
          case 2:
            this.lianNew.two = this.dealTwo()
            break;
          default:
            this.lianNew.one = this.dealOne()
            break;
        }
      }
    },
    rangeInt(minNum, maxNum){
      switch(arguments.length){
        case 1:
          return parseInt(Math.random()*minNum+1,10);
          break;
        case 2:
          return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
          break;
        default:
          return 0;
          break;
      }
    },
    lianBao (type){
      if(type == 1){
        this.lianOld = JSON.parse(JSON.stringify(this.lianNew))
      }
      this.lianNew.one = ''
      this.lianNew.two = ''
      this.lianNew.three = ''
      this.lianNew.four = ''
      this.lianNew.five = ''
    },
    initFiveList (){
      let fiveList = []
      for(let i in this.fiveOriginList){
        //
        if(this.fiveOriginList[i].type == 1){
          //
          let jineng = {
            name: this.fiveOriginList[i].name + ': ',
            section: [0.5, 1, 1.5, 2]
          }
          fiveList.push(jineng)
        }
        let jili = {
          name: this.fiveOriginList[i].name + ': ',
          section: [1, 2, 3, 4]
        }
        fiveList.push(jili)
      }
      this.fiveList = JSON.parse(JSON.stringify(fiveList))
      // console.log(this.fiveList)
    },
    dealFive (){
      let fiveList = this.fiveList
      let length = fiveList.length
      let xiabiao = this.rangeInt(0, length - 1)
      let fiveSingle = fiveList[xiabiao]
      let numXiabiao = this.rangeInt(0, fiveSingle.section.length - 1)
      return fiveSingle.name + fiveSingle.section[numXiabiao] + '%'
    },
    dealFour (){
      let fourList = this.fourList
      let length = fourList.length
      let fourSingle = []
      if(this.lianLock.lockFour == true){
        //
        let str = this.lianOld.four.replace(/\d/g, '')
        fourSingle = this.$find_array(str, fourList)
        // oneSingle.section[0] = parseInt(this.lianOld.one.replace(/[^0-9]/ig,""))
      }else{
        let xiabiao = this.rangeInt(0, length - 1)
        fourSingle = fourList[xiabiao]
      }
      let number = this.rangeInt(fourSingle.section[0], fourSingle.section[1])
      return fourSingle.name + number
    },
    dealThree (){
      let threeList = this.threeList
      let length = threeList.length
      let threeSingle = []
      if(this.lianLock.lockThree == true){
        //
        let str = this.lianOld.three.replace(/\d/g, '')
        threeSingle = this.$find_array(str, threeList)
        if(this.lianLock.lockThreeBig == true){
          threeSingle.section[0] = parseInt(this.lianOld.three.replace(/[^0-9]/ig,""))
          threeSingle.section[1] = parseInt(this.lianOld.three.replace(/[^0-9]/ig,""))
        }
      }else{
        let xiabiao = this.rangeInt(0, length - 1)
        threeSingle = threeList[xiabiao]
      }
      let number = this.rangeInt(threeSingle.section[0], threeSingle.section[1])
      return threeSingle.name + number
    },
    dealTwo (){
      let twoList = this.twoList
      let length = twoList.length
      let twoSingle = []
      if(this.lianLock.lockTwo == true){
        //
        let num = this.lianOld.two.replace(/[^0-9]/ig,"")
        let str = this.lianOld.two.replace(num, '#@#')
        console.log(num, str, twoList)
        twoSingle = this.$find_array(str, twoList)
        // oneSingle.section[0] = parseInt(this.lianOld.one.replace(/[^0-9]/ig,""))
      }else{
        let xiabiao = this.rangeInt(0, length - 1)
        twoSingle = twoList[xiabiao]
      }
      let number = this.rangeInt(twoSingle.section[0], twoSingle.section[1])
      return twoSingle.name.replace('#@#', number)
    },
    dealOne (){
      let oneList = this.oneList
      let length = oneList.length
      let oneSingle = []
      if(this.lianLock.lockOne == true){
        //
        let str = this.lianOld.one.replace(/\d/g, '')
        oneSingle = this.$find_array(str, oneList)
        // oneSingle.section[0] = parseInt(this.lianOld.one.replace(/[^0-9]/ig,""))
      }else{
        let xiabiao = this.rangeInt(0, length - 1)
        oneSingle = oneList[xiabiao]
      }
      let number = this.rangeInt(oneSingle.section[0], oneSingle.section[1])
      return oneSingle.name + number
    },
    lianLocking (type){
      let number = this.checkLock()
      switch (parseInt(type)) {
        case 5:
          if(this.lianLock.lockThree == false && number >= 3){
            this.$Message.warning('')
            return false
          }
          //
          if(this.lianLock.lockThree == false && this.lianLock.lockThreeBig == false){
            this.$Message.warning('')
            return false
          }
          this.lianLock.lockThreeBig = !this.lianLock.lockThreeBig
          break;
        case 4:
          if(this.lianOld.four == '')
            return false;
          if(this.lianLock.lockFour == false && number >= 3){
            this.$Message.warning('')
            return false
          }
          this.lianLock.lockFour = !this.lianLock.lockFour
          break;
        case 3:
          if(this.lianOld.three == '')
            return false;
          if(this.lianLock.lockThree == false && number >= 3){
            this.$Message.warning('')
            return false
          }
          if(this.lianLock.lockThree == true){
            this.lianLock.lockThreeBig = false
          }
          this.lianLock.lockThree = !this.lianLock.lockThree
          break;
        case 2:
          if(this.lianOld.two == '')
            return false;
          if(this.lianLock.lockTwo == false && number >= 3){
            this.$Message.warning('')
            return false
          }
          this.lianLock.lockTwo = !this.lianLock.lockTwo
          break;
        default:
          if(this.lianOld.one == '')
            return false;
          if(this.lianLock.lockOne == false && number >= 3){
            this.$Message.warning('')
            return false
          }
          this.lianLock.lockOne = !this.lianLock.lockOne
          break;
      }
    },
    checkLock (){
      let number = 0
      this.lianLock.lockOne == true?number++:''
      this.lianLock.lockTwo == true?number++:''
      this.lianLock.lockThree == true?number++:''
      this.lianLock.lockFour == true?number++:''
      return number
    },
    checkLianList (pool){
      let arr = []
      let poolList = pool
      if(this.lianNew.one != '')
        arr.push(1)
      if(this.lianNew.two != '')
        arr.push(2)
      if(this.lianNew.three != '')
        arr.push(3)
      if(this.lianNew.four != '')
        arr.push(4)
      if(this.lianNew.five != '')
        arr.push(5)
      // console.log(poolList, arr)
      return this.$array_diff(poolList, arr)
    },
    checkNewIsEmpty (){
      let re = true
      if(this.lianNew.one != ''){
        re = false
        return re
      }
      if(this.lianNew.two != ''){
        re = false
        return re
      }
      if(this.lianNew.three != ''){
        re = false
        return re
      }
      if(this.lianNew.four != ''){
        re = false
        return re
      }
      if(this.lianNew.five != ''){
        re = false
        return re
      }
      return re
    },
    checkOldIsEmpty (){
      let re = true
      if(this.lianOld.one != ''){
        re = false
        return re
      }
      if(this.lianOld.two != ''){
        re = false
        return re
      }
      if(this.lianOld.three != ''){
        re = false
        return re
      }
      if(this.lianOld.four != ''){
        re = false
        return re
      }
      if(this.lianOld.five != ''){
        re = false
        return re
      }
      return re
    },
    deleteArr (value, arr){
      let xiabiao = arr.indexOf(value)
      if(xiabiao >= 0){
        arr.splice(xiabiao, 1)
      }
      return arr
    }
  }
}
